body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
}

a {
	color: inherit;
}

.title {
    margin-top: 0;
    text-align: right;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 32px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    color: #132029;
    position: relative;
    z-index: 1;
    line-height: 1.75;
    letter-spacing: -1.5px;
    padding: 0 24px;
    margin-bottom: -16px;
}

@media(min-width: 750px) {
    .title {
        font-size: 64px;
        line-height: 0.88;
        padding: 0 65px;
        margin-bottom: 0;
        letter-spacing: -3px;
        margin-bottom: -3px;
    }
}

@media(min-width: 1200px) {
    .title {
        padding: 0;
    }
}

.title a {
	text-decoration: none;
	color: inherit;
}
.valid-seed__select-container .pages-select__block:first-child {
  padding-right: 22px;
}

.pages-select__container {
  text-align: center;
  position: relative;
  margin-bottom: 30px;
}

@media(min-width:1200px) {
  .pages-select__container:before {
    content: '';
    background-color: #d2ded9;
    height: 1px;
    width: 100%;
    display: block;
    position: absolute;
    top: 20px;
  }
}

.pages-select__container-position {
  display: inline-block;
  margin: 0 auto;
  background-color: #fff;
  position: relative;
}

@media(min-width: 750px) {
  .pages-select__container-position {
    padding: 0 7px 0 15px;
  }
}

.pages-select__container-position .pages-select__block + .pages-select__block:not(.pages-select__block--words) {
  margin-left: 40px;
}


@media(min-width: 750px) {
  .pages-select__container-position .pages-select__block + .pages-select__block {
    margin-left: 70px;
  }
  .pages-select__container-position .pages-select__block--words .pages-select__control {
    width: 58px;
  }
}

@media(min-width: 1200px) {
  .pages-select__container-position .pages-select__block + .pages-select__block {
    margin-left: 32px;
  }
}

.pages-select__container-position--words .pages-select__block {
  margin-left: 0;
}

.pages-select__block {
  line-height: 1.4;
  display: inline-block;
}

.pages-select__block--words {
  display: block;
}

@media(min-width: 750px) {
  .pages-select__block--words {
    display: inline-block;
  }
}

.pages-select__block>div {
  display: inline-block;
  vertical-align: middle;
}

.pages-select__block>div:hover svg, .pages-select__block>div:active svg {
  fill: #6da7ac;
}

.pages-select__block .pages-select__single-value {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  line-height: 32px;
  color: rgba(0, 0, 0, 0.91);
}

@media(min-width:750px) {
  .pages-select__block .pages-select__single-value {
    font-size: 24px;
  }
}

