  width: 250px;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.26), 1px 1px 2px 0 rgba(0, 0, 0, 0.17);
  border-radius: 0px;
  margin: 0;
  bottom: 100%;
  top: unset;
  right: -90px;
}

@media(min-width: 750px) {
  .pages-select__block .pages-select__menu {
    right: -288px;
    width: 336px;
  }
  .pages-select__block--words .pages-select__menu {
    right: 0;
  }
}

@media(min-width: 1200px) {
