.seed-container__textarea::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.4);
    text-shadow: none;
    -webkit-text-fill-color: initial;
}

.seed-container__textarea:focus {
    outline: none;
}

.seed-container__text {
    position: relative;
    display: inline-block;
    font-size: 18px;
    line-height: 1.71;
    margin: 0;
    padding-right: 5px;
    color: #000;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: normal;
}

@media(min-width: 750px) {
    .seed-container__text {
        font-size: 36px;
        padding-right: 10px;
    }
}

@media(min-width: 1200px) {
    .seed-container__text {
        font-size: 28px;
        padding-right: 8px;
    }
}

.seed-container__text--invalid {
    color: rgba(127, 23, 23, 0.9);
}

.seed-container__placeholder {
    pointer-events: none;
    position: absolute;
    top: 0;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.72);
    font-size: 18px;
    line-height: 1.71;
}

@media(min-width: 750px) {
    .seed-container__placeholder {
        font-size: 36px;
        line-height: 1.33;
    }
}

@media(min-width:1200px) {
    .seed-container__placeholder {
        font-size: 28px;
        line-height: 1.71;
    }
}

.seed-container__placeholder--focus {
    opacity: 0.5;
}

.seed-container__placeholder--hidden {
    display: none;
}

.caption {
    font-family: 'Charter-Roman', serif;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    text-align: left;
    color: rgba(0, 0, 0, 0.72);
    margin-top: 0;
    margin-bottom: 0;
    font-size: 11px;
    letter-spacing: 0;
    line-height: 1.73;
    margin-top: 30px;
}

@media(min-width:750px) {
    .caption {
        font-size: 15px;
        line-height: 2.13;
        letter-spacing: 0.2px;
    }
}

@media(min-width:1200px) {
    .caption {
        line-height: 1.87;
        letter-spacing: -0.1px;
    }
}

.caption--restore-seed {
    margin-top: 0;
    text-align: center;
    position: absolute;
    width: 100%;
    bottom: 21px;
    box-sizing: border-box;
    padding: 0 15px;
}

@media(min-width: 750px) {
    .caption--restore-seed {
        bottom: 24px;
    }
}

@media(min-width: 1600px) {
    .caption--restore-seed {
        bottom: 14px;
    }
}

.caption--invalid {
    color: #8a2c2c;
}

.footer a {
    text-decoration: none;
    color: inherit;
}

